import React, { useState, useEffect } from 'react';
import { Link, useNavigate, useLocation } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';

const Navbar = () => {
    const { user, isAuthenticated, logout } = useAuth();
    const navigate = useNavigate();
    const location = useLocation();
    const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);

    const handleLogout = () => {
        logout();
        navigate('/');
        setIsMobileMenuOpen(false);
    };

    const isActiveRoute = (path) => {
        return location.pathname === path;
    };

    const toggleMobileMenu = () => {
        setIsMobileMenuOpen(!isMobileMenuOpen);
    };

    const closeMobileMenu = () => {
        setIsMobileMenuOpen(false);
    };

    return (
        <nav style={styles.navbar}>
            <div style={styles.container}>
                {/* Logo */}
                <Link to="/" style={styles.logo} onClick={closeMobileMenu}>
                    <div style={styles.logoContainer}>
                        <span style={styles.logoIcon}></span>
                        AI Resume Builder
                    </div>
                </Link>

                {/* Desktop Navigation */}
                <div style={styles.desktopNav}>
                    {isAuthenticated ? (
                        <>
                            <Link
                                to="/dashboard"
                                style={{
                                    ...styles.navLink,
                                    ...(isActiveRoute('/dashboard') && styles.activeNavLink)
                                }}
                            >
                                Dashboard
                            </Link>
                            <Link
                                to="/builder"
                                style={{
                                    ...styles.navLink,
                                    ...(isActiveRoute('/builder') && styles.activeNavLink)
                                }}
                            >
                                Builder
                            </Link>
                            <Link
                                to="/analyzer"
                                style={{
                                    ...styles.navLink,
                                    ...(isActiveRoute('/analyzer') && styles.activeNavLink)
                                }}
                            >
                                Analyzer
                            </Link>
                            <Link
                                to="/profile"
                                style={{
                                    ...styles.navLink,
                                    ...(isActiveRoute('/profile') && styles.activeNavLink)
                                }}
                            >
                                Profile
                            </Link>
                            <Link
                                to="/templates"
                                style={{
                                    ...styles.navLink,
                                    ...(isActiveRoute('/templates') && styles.activeNavLink)
                                }}
                            >
                                Templates
                            </Link>

                            <div style={styles.userSection}>
                                <div style={styles.userInfo}>
                                    <div style={styles.userAvatar}>
                                        {user?.name ? user.name.charAt(0).toUpperCase() : 'U'}
                                    </div>
                                    <span style={styles.welcome}>
                                        Hi, {user?.name ? user.name.split(' ')[0] : 'User'}
                                    </span>
                                </div>
                                <button onClick={handleLogout} style={styles.logoutBtn}>
                                    Logout
                                </button>
                            </div>
                        </>
                    ) : (
                        <>
                            <Link
                                to="/"
                                style={{
                                    ...styles.navLink,
                                    ...(isActiveRoute('/') && styles.activeNavLink)
                                }}
                            >
                                Home
                            </Link>
                            <Link
                                to="/builder"
                                style={styles.navLink}
                            >
                                Builder
                            </Link>
                            <button
                                onClick={() => navigate('/analyzer')}
                                style={styles.secondaryButton}
                            >
                                Analyze
                            </button>
                            <div style={styles.authButtons}>
                                <Link to="/login" style={styles.loginBtn}>
                                    Login
                                </Link>
                                <Link to="/register" style={styles.registerBtn}>
                                    Get Started
                                </Link>
                            </div>
                        </>
                    )}
                </div>

                {/* Mobile Menu Button */}
                <button
                    style={styles.mobileMenuButton}
                    onClick={toggleMobileMenu}
                    aria-label="Toggle menu"
                >
                    <span style={isMobileMenuOpen ? styles.hamburgerOpen : styles.hamburger}>
                        <span style={styles.hamburgerLine}></span>
                        <span style={styles.hamburgerLine}></span>
                        <span style={styles.hamburgerLine}></span>
                    </span>
                </button>

                {/* Mobile Navigation */}
                {isMobileMenuOpen && (
                    <div style={styles.mobileNav}>
                        {isAuthenticated ? (
                            <>
                                <div style={styles.mobileUserInfo}>
                                    <div style={styles.userAvatar}>
                                        {user?.name?.charAt(0).toUpperCase()}
                                    </div>
                                    <span style={styles.mobileWelcome}>
                                        Welcome, {user?.name || 'User'}
                                    </span>
                                </div>
                                <Link
                                    to="/dashboard"
                                    style={styles.mobileNavLink}
                                    onClick={closeMobileMenu}
                                >
                                     Dashboard
                                </Link>
                                <Link
                                    to="/builder"
                                    style={styles.mobileNavLink}
                                    onClick={closeMobileMenu}
                                >
                                     Builder
                                </Link>
                                <Link
                                    to="/analyzer"
                                    style={styles.mobileNavLink}
                                    onClick={closeMobileMenu}
                                >
                                     Analyzer
                                </Link>
                                <Link
                                    to="/profile"
                                    style={styles.mobileNavLink}
                                    onClick={closeMobileMenu}
                                >
                                     Profile
                                </Link>
                                <Link
                                    to="/templates"
                                    style={styles.mobileNavLink}
                                    onClick={closeMobileMenu}
                                >
                                     Templates
                                </Link>
                                <button
                                    onClick={handleLogout}
                                    style={styles.mobileLogoutBtn}
                                >
                                     Logout
                                </button>
                            </>
                        ) : (
                            <>
                                <Link
                                    to="/"
                                    style={styles.mobileNavLink}
                                    onClick={closeMobileMenu}
                                >
                                     Home
                                </Link>
                                <Link
                                    to="/builder"
                                    style={styles.mobileNavLink}
                                    onClick={closeMobileMenu}
                                >
                                     Builder
                                </Link>
                                <Link
                                    to="/analyzer"
                                    style={styles.mobileNavLink}
                                    onClick={closeMobileMenu}
                                >
                                     Analyzer
                                </Link>
                                <Link
                                    to="/login"
                                    style={styles.mobileNavLink}
                                    onClick={closeMobileMenu}
                                >
                                     Login
                                </Link>
                                <Link
                                    to="/register"
                                    style={styles.mobileRegisterBtn}
                                    onClick={closeMobileMenu}
                                >
                                     Get Started
                                </Link>
                            </>
                        )}
                    </div>
                )}
            </div>

            {/* Overlay for mobile menu */}
            {isMobileMenuOpen && (
                <div style={styles.overlay} onClick={closeMobileMenu}></div>
            )}
        </nav>
    );
};

