// NOTE: Avoid performing DOM operations at module import time. Previously we
// attempted to inject CSS rules using `document.styleSheets[0]` which can
// throw during import and crash the app before React renders. Instead we
// optionally inject responsive CSS at runtime inside a useEffect.

// This effect appends a <style> tag to the document head when the component
// mounts and removes it on unmount. It is defensive and will silently skip
// if `document` is not available for any reason.
const injectedResponsiveCSS = `
@media (max-width: 768px) {
  .desktop-nav { display: none !important; }
  .mobile-menu-button { display: block !important; }
}
@media (min-width: 769px) {
  .mobile-nav { display: none !important; }
  .overlay { display: none !important; }
}
`;

export default Navbar;